import { Injectable } from '@angular/core';
//import HttpClient vào để sử dụng
import { HttpClient } from '@angular/common/http';
//import Model để chứa dl
import { chitiet } from './../models/chitiet.model';
//import kiểu dữ liệu Observable
import { Observable } from 'rxjs';
@Injectable({
  providedIn: 'root'
})
export class ChitietService {

  public API : string = 'http://localhost:3000/chitiet';

  constructor(public http : HttpClient) { }

  //List danh sách  các mặt hàng
  getAllchitiet() {
    return this.http.get(this.API);
  } 
 getOnechitiet(id: number) {
    return this.http.get(`${this.API}/${id}`);
  }
  add_chitiet(chitiet : chitiet) {
	return this.http.post(this.API, chitiet);  	
  }
   delete_chitiet(id : number) {
      return this.http.delete(`${this.API}/${id}`);
  }
  uppdate_chitiet(chitiet : chitiet) {
      return this.http.put(`${this.API}/${chitiet.id}`,chitiet);
  }

}
