import { Injectable } from '@angular/core';
//import HttpClient vào để sử dụng
import { HttpClient } from '@angular/common/http';
//import Model để chứa dl
import { hoadon } from './../models/hoadon.model';
//import kiểu dữ liệu Observable
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class HoadonService {

  public API : string = 'http://localhost:3000/hoadon';

  constructor(public http : HttpClient) { }

  //List danh sách  các mặt hàng
  getAllhoadon() {
  	return this.http.get(this.API);
  } 
  getOnehoadon(id: number) {
    return this.http.get(`${this.API}/${id}`);
  }
  add_hoadon(hoadon : hoadon) {
	return this.http.post(this.API, hoadon);  	
  }
   delete_hoadon(id : number) {
      return this.http.delete(`${this.API}/${id}`);
  }
  uppdate_hoadon(hoadon : hoadon) {
      return this.http.put(`${this.API}/${hoadon.id}`,hoadon);
  }
}
