import { Component, OnInit, OnDestroy } from '@angular/core';
import { DmhangService } from './../../../services/dmhang.service';
import { Subscription } from 'rxjs';
import { dmhang } from './../../../models/dmhang.model';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-dmhang-list',
  templateUrl: './dmhang-list.component.html',
  styleUrls: ['./dmhang-list.component.css']
})
export class DmhangListComponent implements OnInit, OnDestroy {

	public subscription : Subscription;
	public db : dmhang[] = [];
  public madiachi : string;
  constructor(public dmhangservice : DmhangService) { }

  ngOnInit() {
  	  this.subscription =	this.dmhangservice.getAlldmhang().subscribe((data: dmhang[]) =>{
  		this.db = data;
  	});
  }
  ngOnDestroy(){
  	if(this.subscription)
  	{
  			this.subscription.unsubscribe();
  	}
  }
 onDelete_dmhang(id: number){
      this.subscription =  this.dmhangservice.delete_dmhang(id).subscribe((data: dmhang) =>{
      this.updateDataAfterDelete(id);
       });
  }
  updateDataAfterDelete(id: number){
    for(var i = 0; i < this.db.length; i++)
    {
      if(this.db[i].id == id)
      {
        this.db.splice(i, 1);
        break;
      }
    }
  }
}