import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'sorthoadon'
})
export class SorthoadonPipe implements PipeTransform {

  transform(hoadons: any[], sortBy : string, sortValue : number): any {
  	if(sortBy === 'MAKH')
  	{
  		hoadons.sort((a,b) =>{
			if(a.makh > b.makh) {
				return sortValue;
			}  else if(a.makh < b.makh){
				return -sortValue;
			}			else return 0;
  		});
  	}
  	if(sortBy === 'THANHTOAN')
  	{
  		hoadons.sort((a,b) =>{
			if(a.tratien > b.tratien) {
				return sortValue;
			}  else if(a.tratien < b.tratien){
				return -sortValue;
			}			else return 0;
  		});
  	}
  	if(sortBy === 'MAHOADON')
  	{
  		hoadons.sort((a,b) =>{
			if(a.mahoadon > b.mahoadon) {
				return sortValue;
			}  else if(a.mahoadon < b.mahoadon){
				return -sortValue;
			}			else return 0;
  		});
  	}
    return hoadons;
  }

}
