import { Component, OnInit } from '@angular/core';
import { DmkhService } from './../../../services/dmkh.service';
import { DmdiachiService } from './../../../services/dmdiachi.service';
import { Subscription } from 'rxjs';
import { dmkh } from './../../../models/dmkh.model';
import { dmdiachi } from './../../../models/dmdiachi.model';
@Component({
  selector: 'app-dmkh-list',
  templateUrl: './dmkh-list.component.html',
  styleUrls: ['./dmkh-list.component.css']
})
export class DmkhListComponent implements OnInit {

  	public subscription : Subscription;
  public db : dmkh[] = [];
	public db_dmdiachi : dmdiachi[] = [];
  constructor(public dmkhservice : DmkhService, public dmdiachi : DmdiachiService) { }

  ngOnInit() {
  	this.subscription =	this.dmkhservice.getAlldmkh().subscribe((data: dmkh[]) =>{
  		this.db = data;
  	});
    this.subscription =  this.dmdiachi.getAlldmdiachi().subscribe((data: dmdiachi[]) =>{
      this.db_dmdiachi = data;
    });
  }
  ngOnDestroy(){
  	if(this.subscription)
  	{
  			this.subscription.unsubscribe();
  	}
  }
  onDelete_dmkh(id: number){
    console.log(id);
      this.subscription =  this.dmkhservice.delete_dmkh(id).subscribe((data: dmkh) =>{
      this.updateDataAfterDelete(id);
       });
  }
  updateDataAfterDelete(id: number){
    for(var i = 0; i < this.db.length; i++)
    {
      if(this.db[i].id == id)
      {
        this.db.splice(i, 1);
        break;
      }
    }
  }

}
