import { Component, OnInit, OnDestroy } from '@angular/core';
import { DmhangService } from './../../../services/dmhang.service';
import { DmkhService } from './../../../services/dmkh.service';
import { DmdiachiService } from './../../../services/dmdiachi.service';
import { ChitietService } from './../../../services/chitiet.service';
import { ChitiethdService } from './../../../services/chitiethd.service';
import { HoadonService } from './../../../services/hoadon.service';
import { Subscription } from 'rxjs';
import { dmhang } from './../../../models/dmhang.model';
import { dmkh } from './../../../models/dmkh.model';
import { dmdiachi } from './../../../models/dmdiachi.model';
import { chitiet } from './../../../models/chitiet.model';
import { chitiethd } from './../../../models/chitiethd.model';
import { hoadon } from './../../../models/hoadon.model';
import { HttpClient } from '@angular/common/http';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { DatePipe } from '@angular/common';


@Component({
  selector: 'app-chitiethd-edit',
  templateUrl: './chitiethd-edit.component.html',
  styleUrls: ['./chitiethd-edit.component.css']
})
export class ChitiethdEditComponent implements OnInit, OnDestroy {

  	public subscription : Subscription;
   public subscriptionParams : Subscription;
	public db_dmhang : dmhang[] = [];
  public db_dmkh : dmkh[] = [];
  public db_dmdiachi : dmdiachi[] = [];
  public db_chitiet : chitiet[] = [];
  public db_chitiethd : chitiethd[] = [];
	public db_hoadon : hoadon[] = [];
	public db : hoadon;
  public new_hoadon : hoadon;
  public new_chitiethd : chitiethd;
  public madiachi : string;
  public check_huyen : string;
  today = Date.now();
    fixedTimezone = this.today;
    public tongtt : number = 0;
    public mahoadon : string;
    public txt_makh : string;
    public dem : number = 1;
    public conlai : number;
    public thanhtoan : number;
    public tiencu : number = 0;
    public tongcong : number = 0;
    public demhd : number=-1;
  constructor(
  	public dmhangservice : DmhangService,
    public dmkhservice : DmkhService,
    public dmdiachiservice : DmdiachiService,
    public chitietservice : ChitietService,
    public chitiethdservice : ChitiethdService,
  	public hoadonservice : HoadonService,
    public routerService : Router,
    public activeRouterService : ActivatedRoute,
    private datePipe: DatePipe

  	) { }

  ngOnInit() {
  	  this.subscription =	this.dmhangservice.getAlldmhang().subscribe((data: dmhang[]) =>{
  		this.db_dmhang = data;});
  		this.subscription =	this.dmkhservice.getAlldmkh().subscribe((data: dmkh[]) =>{
  		this.db_dmkh = data; });
      this.subscription =  this.dmdiachiservice.getAlldmdiachi().subscribe((data: dmdiachi[]) =>{
      this.db_dmdiachi = data; });
  	this.subscription =  this.hoadonservice.getAllhoadon().subscribe((data: hoadon[]) =>{
      this.db_hoadon = data; });
    //var date = new Date();
   //this.mahoadon = this.datePipe.transform(date,"ddMMyyyy");
    this.subscription =  this.chitietservice.getAllchitiet().subscribe((data: chitiet[]) =>{
      this.db_chitiet = data; });
   this.subscription =  this.chitiethdservice.getAllchitiethd().subscribe((data: chitiethd[]) =>{
      this.db_chitiethd = data; });
   this.db = new hoadon();
  	this.loadData();
  	this.tongtt = this.db.tonghoadon;
  	this.thanhtoan = this.db.tratien;
  	this.conlai = this.db.conlai;
  }
  ngOnDestroy(){
  	if(this.subscription)
  	{
  			this.subscription.unsubscribe();
  	}
  }
 loadData(){
  	this.subscriptionParams = this.activeRouterService.params.subscribe((data : Params) =>{
  			let id = data['id'];
  			this.subscription = this.hoadonservice.getOnehoadon(id).subscribe((hoadon: hoadon) =>{
  					this.db = hoadon;
  			});
  		});

 }

	 valuechange(chitiet : chitiethd){
   this.tongcong = 0 ;
    if(chitiet.sl !=null && chitiet.dg !=null)
    {
        chitiet.tt = chitiet.sl * chitiet.dg;
    }else{
      chitiet.tt = null;
    }
    this.tongtt =0;
  for (var i = this.db_chitiethd.length - 1; i >= 0; i--) {
  	if(this.db_chitiethd[i].mahoadon == this.db.mahoadon){

     this.tongtt = this.tongtt + (this.db_chitiethd[i].tt * 1000);
  	}
  }
  this.tiencu = 0;
  for (var i = this.db_hoadon.length - 1; i >= 0; i--) {
  	if(this.db_hoadon[i].makh == this.db.makh && this.db_hoadon[i].id < this.db.id ){
  		this.tiencu = this.tiencu + this.db_hoadon[i].conlai;
  	}
  }
  if(this.tongtt >0)
  {
     this.tongcong = this.tongtt + this.tiencu;
  }else{this.tongcong =0;}
 	this.thanhtoan = this.db.tratien;
 	this.conlai = this.tongtt + this.tiencu - this.thanhtoan ;
 }
 Valuethanhtoan(){
   this.conlai = this.tongtt - this.thanhtoan;
   this.tongcong = this.tongtt - this.thanhtoan + this.tiencu;
 }
 checkMahoadon(){
   this.mahoadon = this.mahoadon.slice(0,8);
   this.dem = 1;
   for (var i = this.db_hoadon.length - 1; i >= 0; i--) {
     if(this.db_hoadon[i].mahoadon.slice(0,8) == this.mahoadon.slice(0,8))
     {
       this.dem ++;
     }
   }
  
   if(this.dem <10)
   {
       this.mahoadon = this.mahoadon.slice(0,8) + '00' + this.dem;
   }else if(this.dem <100)
   {
       this.mahoadon = this.mahoadon.slice(0,8) + '0' + this.dem;
   }else { this.mahoadon = this.mahoadon.slice(0,8) + this.dem; }
 }
 checkHoadon(){
   this.tiencu =0;
   this.checkMahoadon();

   for (var i = this.db_hoadon.length - 1; i >= 0; i--) {
     if(this.db_hoadon[i].kt == false && this.db_hoadon[i].makh == this.txt_makh){
         this.tiencu = this.tiencu + this.db_hoadon[i].conlai;
     }
   }
 }

 onEdit_chitiethd(mahoadon: string, mahang : string, sl : number, dvt : string, dg : number, tt : number){
   this.new_chitiethd = new chitiethd();
   this.new_chitiethd.mahoadon = mahoadon;
   this.new_chitiethd.mahang = mahang;
   this.new_chitiethd.sl = sl;
   this.new_chitiethd.DVT = dvt;
   this.new_chitiethd.dg = dg;
   this.new_chitiethd.tt = tt;
   this.subscription = this.chitiethdservice.add_chitiethd(this.new_chitiethd).subscribe(data =>{
    if(data)
      {
      this.routerService.navigate(['hoadon']);
      }
    });
 }
 
}
