import { Injectable } from '@angular/core';
//import HttpClient vào để sử dụng
import { HttpClient } from '@angular/common/http';
//import Model để chứa dl
import { dmkh } from './../models/dmkh.model';
//import kiểu dữ liệu Observable
import { Observable } from 'rxjs';
@Injectable({ 
  providedIn: 'root'
})
export class DmkhService {

	//Khai báo đường dẫn đến Server để lấy dl
	public API : string = 'http://localhost:3000/dmkh';

  constructor(public http : HttpClient) { }
  //List danh sách  các mặt hàng
  getAlldmkh() {
  return this.http.get(this.API);
  } 
  getOnedmkh(id: number) {
    return this.http.get(`${this.API}/${id}`);
  }
  add_dmkh(dmkh : dmkh) {
	return this.http.post(this.API, dmkh);  	
  }
   delete_dmkh(id : number) {
      return this.http.delete(`${this.API}/${id}`);
  }
  uppdate_dmkh(dmkh : dmkh) {
      return this.http.put(`${this.API}/${dmkh.id}`,dmkh);
  }
  
}
