import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filterChitiethd'
})
export class FilterChitiethdPipe implements PipeTransform {

  transform(chitiethds: any[], mahd : string): any {
   if(mahd)
   {
       return chitiethds.filter(item => 
         item.mahoadon.toString().toLocaleLowerCase().indexOf(mahd.toString().toLocaleLowerCase()) !== -1); 
      }else
      {
      	return chitiethds;
      }
   	 
}
}