import { Component, OnInit, OnDestroy } from '@angular/core';
import { HoadonService } from './../../../services/hoadon.service';
import { Subscription } from 'rxjs';
import { hoadon } from './../../../models/hoadon.model';
import { Router, ActivatedRoute, Params } from '@angular/router';

@Component({
  selector: 'app-hoadon-edit',
  templateUrl: './hoadon-edit.component.html',
  styleUrls: ['./hoadon-edit.component.css']
})
export class HoadonEditComponent implements OnInit {
  public tonghoadon : number;
  public tratien : number;
  public conlai : number;
  public subscription : Subscription;
  public subscriptionParams : Subscription;
	//public db : dmhang[] = [];
 	public db : hoadon;
  constructor(
  	public dmhangservice : HoadonService,
  	public routerService : Router,
  	public activeRouterService : ActivatedRoute

  	) { }

 ngOnInit() {
  	this.db = new hoadon();
  	this.loadData();
  }
  loadData(){
  	this.subscriptionParams = this.activeRouterService.params.subscribe((data : Params) =>{
  			let id = data['id'];
  			this.subscription = this.dmhangservice.getOnehoadon(id).subscribe((hoadon: hoadon) =>{
  					this.db = hoadon;
  			});
  		});
  }
  ngOnDestroy(){
  	if(this.subscription)
  	{
  			this.subscription.unsubscribe();
  	}
  	if(this.subscriptionParams)
  	{
  			this.subscriptionParams.unsubscribe();
  	}
  }
onEdit_hoadon(){
	this.subscription = 
	this.dmhangservice.uppdate_hoadon(this.db).subscribe((data: hoadon) =>{
		this.routerService.navigateByUrl('hoadon');
		//console.log(data);
	});
}
Valuethanhtoan(){
  this.db.conlai = this.db.tonghoadon - this.db.tratien;
}
}
