import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-bangke',
  templateUrl: './bangke.component.html',
  styleUrls: ['./bangke.component.css']
})
export class BangkeComponent implements OnInit {
	public sumCash : number;
	public txt500 : number;
	public txt200 : number;
	public txt100 : number;
	public txt50 : number;
	public txt20 : number;
	public txt10 : number;
	public txt5 : number;
	public txt2 : number;
	public txt1 : number;
	public sum500 : number =0 ;
	public sum200 : number =0;
	public sum100 : number =0;
	public sum50 : number =0;
	public sum20 : number =0;
	public sum10 : number =0;
	public sum5 : number =0;
	public sum2 : number =0;
	public sum1 : number =0;
	public sumLast : number =0;
  constructor() { }

  ngOnInit() {
  }

valuechange(){
	if(this.txt500)
	{
		this.sum500 = this.txt500 * 500000;
	}else{this.sum500 = 0;}
	if(this.txt200){
		this.sum200 = this.txt200 * 200000;	
	}else{this.sum200 =0;}
	if(this.txt100){
		this.sum100 = this.txt100 * 100000;	
	}else{this.sum100=0;}
	if(this.txt50){
		this.sum50 = this.txt50 * 50000;	
	}else{this.sum50=0;}
	if(this.txt20){
		this.sum20 = this.txt20 * 20000;	
	}else{this.sum20=0;}
	if(this.txt10){
		this.sum10 = this.txt10 * 10000;	
	}else{this.sum10=0;}
	if(this.txt5){
		this.sum5 = this.txt5 * 5000;	
	}else{this.sum5=0;}
	if(this.txt2){
		this.sum2 = this.txt2 * 2000;	
	}else{this.sum2=0;}
	if(this.txt1){
		this.sum1 = this.txt1 * 1000;	
	}else{this.sum1=0;}
	

	this.sumLast = this.sumCash - (this.sum500 + this.sum200 + this.sum100 + this.sum50 + this.sum20 + this.sum10 + this.sum5 + this.sum2 + this.sum1);
	//console.log(this.sumLast);
}
valueInput(){
	this.sum500 = 0;
	this.sum200 = 0;
	this.sum100 = 0;
	this.sum50 = 0;
	this.sum20 = 0;
	this.sum10 = 0;
	this.sum5 = 0;
	this.sum2 = 0;
	this.sum1 = 0;
	this.txt500 = null;
	this.txt200 = null;
	this.txt100 = null;
	this.txt50 = null;
	this.txt20 = null;
	this.txt10 = null;
	this.txt5 = null;
	this.txt2 = null;
	this.txt1 = null;
	this.sumLast = null;
}
}
