import { Component, OnInit, OnDestroy } from '@angular/core';
import { DmhangService } from './../../../services/dmhang.service';
import { Subscription } from 'rxjs';
import { dmhang } from './../../../models/dmhang.model';
import { Router, ActivatedRoute, Params } from '@angular/router';

@Component({
  selector: 'app-dmhang-edit',
  templateUrl: './dmhang-edit.component.html',
  styleUrls: ['./dmhang-edit.component.css']
})
export class DmhangEditComponent implements OnInit {

  public subscription : Subscription;
  public subscriptionParams : Subscription;
	//public db : dmhang[] = [];
 	public db : dmhang;
  constructor(
  	public dmhangservice : DmhangService,
  	public routerService : Router,
  	public activeRouterService : ActivatedRoute

  	) { }

 ngOnInit() {
  	this.db = new dmhang();
  	this.loadData();
  }
  loadData(){
  	this.subscriptionParams = this.activeRouterService.params.subscribe((data : Params) =>{
  			let id = data['id'];
  			this.subscription = this.dmhangservice.getOnedmhang(id).subscribe((dmhang: dmhang) =>{
  					this.db = dmhang;
  			});
  		});
  }
  ngOnDestroy(){
  	if(this.subscription)
  	{
  			this.subscription.unsubscribe();
  	}
  	if(this.subscriptionParams)
  	{
  			this.subscriptionParams.unsubscribe();
  	}
  }
onEdit_dmhang(){
	this.subscription = 
	this.dmhangservice.uppdate_dmhang(this.db).subscribe((data: dmhang) =>{
		this.routerService.navigateByUrl('dmhang');
		//console.log(data);
	});
}
}
