import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'tenhang'
})
export class TenhangPipe implements PipeTransform {

 transform(dmhangs: any[], mahangs : string): any {
   if(!mahangs)
   {
   	return dmhangs;	
   }else
   if(mahangs)
   {
   		
   		 return dmhangs.filter(item => 
   			item.mahang.toString().toLocaleLowerCase().indexOf(mahangs.toLocaleLowerCase()) !== -1);

   }else
   {
   	return dmhangs;
   
  }
  }

}
