import { Component, OnInit } from '@angular/core';
import { DmkhService } from './../../../services/dmkh.service';
import { Subscription } from 'rxjs';
import { dmkh } from './../../../models/dmkh.model';
import { Router, ActivatedRoute, Params } from '@angular/router';

@Component({
  selector: 'app-dmkh-edit',
  templateUrl: './dmkh-edit.component.html',
  styleUrls: ['./dmkh-edit.component.css']
})
export class DmkhEditComponent implements OnInit {

  public subscription : Subscription;
  public subscriptionParams : Subscription;
	//public db : dmhang[] = [];
 	public db : dmkh;
  constructor(
  	public dmhangservice : DmkhService,
  	public routerService : Router,
  	public activeRouterService : ActivatedRoute

  	) { }

  ngOnInit() {
  	this.db = new dmkh();
  	this.loadData();
  }
  loadData(){
  	this.subscriptionParams = this.activeRouterService.params.subscribe((data : Params) =>{
  			let id = data['id'];
  			this.subscription = this.dmhangservice.getOnedmkh(id).subscribe((dmkh: dmkh) =>{
  					this.db = dmkh;
  			});
  		});
  }
  ngOnDestroy(){
  	if(this.subscription)
  	{
  			this.subscription.unsubscribe();
  	}
  	if(this.subscriptionParams)
  	{
  			this.subscriptionParams.unsubscribe();
  	}
  }
onEdit_dmkh(){
	this.subscription = 
	this.dmhangservice.uppdate_dmkh(this.db).subscribe((data: dmkh) =>{
		this.routerService.navigateByUrl('dmkh');
		//console.log(data);
	});
}

}
