import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'showChitiethd'
})
export class ShowChitiethdPipe implements PipeTransform {

  transform(chitiethds: any[], mahd : string): any {
    if(!mahd)
   {
   	return chitiethds;	
   }else
   if(mahd)
   {
   		
   		 return chitiethds.filter(item => 
   			item.mahoadon.toString().toLocaleLowerCase().indexOf(mahd.toLocaleLowerCase()) !== -1);
   }else
   {
   	return chitiethds;
   
  }
	}
}
