 import { Component, OnInit} from '@angular/core';
 import { DmhangService } from './../../../services/dmhang.service';
import { DmkhService } from './../../../services/dmkh.service';
import { ChitietService } from './../../../services/chitiet.service';
import { ChitiethdService } from './../../../services/chitiethd.service';
import { HoadonService } from './../../../services/hoadon.service';
import { Subscription } from 'rxjs';
import { dmhang } from './../../../models/dmhang.model';
import { dmkh } from './../../../models/dmkh.model';
import { chitiet } from './../../../models/chitiet.model';
import { chitiethd } from './../../../models/chitiethd.model';
import { hoadon } from './../../../models/hoadon.model';
import { HttpClient } from '@angular/common/http';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { DatePipe } from '@angular/common';
import { ChitiethdPipe } from './../../../pipes/chitiethd.pipe';
import { FormsModule } from '@angular/forms';
@Component({
  selector: 'app-hoadons',
  templateUrl: './hoadons.component.html',
  styleUrls: ['./hoadons.component.css']
})
export class HoadonsComponent implements OnInit{

  public sortBy : string = 'MAHOADON';
  public sortValue : number = 1;
 	public subscription : Subscription;
   public subscriptionParams : Subscription;
	public db_dmhang : dmhang[] = [];
  public db_dmkh : dmkh[] = [];
  public db_chitiet : chitiet[] = [];
  public db_chitiethd : chitiethd[] = [];
  public db_hoadon : hoadon[] = [];
	public db_hoadon1 : hoadon[] = [];
  public new_hoadon : hoadon;
  public new_chitiethd : chitiethd;
  	public db : hoadon;
  	public db_hd : chitiethd[] = [];
  today = Date.now();
    fixedTimezone = this.today;
    public tongtt : number = 0;
    public mahoadon : string;
    public mahd : string;
    public tenkhs : string;
    public txt_makh : string;
    public dem : number = 1;
    public conlai : number;
    public thanhtoan : number;
    public tiencu : number = 0;
    public tongcong : number = 0;
    public id_tam : string;
    public show : boolean = false;
    public show_chua : boolean = true;
    public show_giao : boolean = true;
    public ngay : string;
    public kttt : boolean = false;
    public test_tonghoadon : number =0;
    public test_tratien : number =0;
    public test_conlai : number =0;
    public showgiao : number;
    public showchua : number;

  constructor(
	public dmhangservice : DmhangService,
    public dmkhservice : DmkhService,
    public chitietservice : ChitietService,
    public chitiethdservice : ChitiethdService,
  	public hoadonservice : HoadonService,
    public routerService : Router,
    public activeRouterService : ActivatedRoute,
    private datePipe: DatePipe,

  	) { }

  ngOnInit() {
     this.subscription =	this.dmhangservice.getAlldmhang().subscribe((data: dmhang[]) =>{
  		this.db_dmhang = data;});
  		this.subscription =	this.dmkhservice.getAlldmkh().subscribe((data: dmkh[]) =>{
  		this.db_dmkh = data; });
      this.subscription =  this.chitietservice.getAllchitiet().subscribe((data: chitiet[]) =>{
      this.db_chitiet = data; });
      this.subscription =  this.chitiethdservice.getAllchitiethd().subscribe((data: chitiethd[]) =>{
      this.db_chitiethd = data; });
  	this.subscription =  this.hoadonservice.getAllhoadon().subscribe((data: hoadon[]) =>{
      this.db_hoadon = data; });
     this.subscription =  this.hoadonservice.getAllhoadon().subscribe((data: hoadon[]) =>{
      this.db_hoadon1 = data; });
    var date = new Date();
   this.mahoadon = this.datePipe.transform(date,"ddMMyyyy");
  }
   Clear_Form(){
      for (var i = this.db_chitiet.length - 1; i >= 0; i--) {
       this.db_chitiet[i].hoadon = null;
       this.db_chitiet[i].mahang = null;
       this.db_chitiet[i].sl = null;
       this.db_chitiet[i].DVT = null;
       this.db_chitiet[i].dg = null;
       this.db_chitiet[i].tt = null;
     } 
      this.tongtt  = 0;
      this.tiencu  = 0;
      this.txt_makh = '';
      this.conlai = 0;
      this.thanhtoan =0;
      this.tongcong =0;
  }
  Update_Form()
  {
    this.subscription =  this.dmhangservice.getAlldmhang().subscribe((data: dmhang[]) =>{
      this.db_dmhang = data;});
      this.subscription =  this.dmkhservice.getAlldmkh().subscribe((data: dmkh[]) =>{
      this.db_dmkh = data; });
      this.subscription =  this.chitietservice.getAllchitiet().subscribe((data: chitiet[]) =>{
      this.db_chitiet = data; });
      this.subscription =  this.chitiethdservice.getAllchitiethd().subscribe((data: chitiethd[]) =>{
      this.db_chitiethd = data; });
    this.subscription =  this.hoadonservice.getAllhoadon().subscribe((data: hoadon[]) =>{
      this.db_hoadon = data; });
  }
  onEdit_hoadon(id : number){
  	this.db = new hoadon();
  	for (var i = this.db_hoadon.length - 1; i >= 0; i--) {
  		if(this.db_hoadon[i].id == id)
  		{
  			this.db.id = this.db_hoadon[i].id;
  			this.db.mahoadon = this.db_hoadon[i].mahoadon;
  			this.db.makh = this.db_hoadon[i].makh;
  			this.db.tonghoadon = this.db_hoadon[i].tonghoadon;
  			this.db.tratien = this.db_hoadon[i].tratien;
  			this.db.conlai = this.db_hoadon[i].conlai;
  			this.db.kt = this.db_hoadon[i].kt;
  			this.db.tinhtrang = !this.db_hoadon[i].tinhtrang;
  		}  		
  	}
	this.subscription = 
	this.hoadonservice.uppdate_hoadon(this.db).subscribe((data: hoadon) =>{
		this.routerService.navigateByUrl('hoadon');
		//console.log(data);
	});
	//this.Update_Form();
	this.ngOnInit();
}
list_chitiethd(mahd : string,makh : string,id : number,thanhtoan : number,
  conlai :number,tonghoadon :number, chua : boolean, giao : boolean, show : boolean){
	this.mahd = mahd;
	this.ngay = mahd.slice(0,2) + "/" + mahd.slice(2,4) + "/" + mahd.slice(4,8);
	for (var i = this.db_chitiethd.length - 1; i >= 0; i--) {
		if(this.db_chitiethd[i].mahoadon == mahd ){
			this.db_hd[i] = this.db_chitiethd[i];
			//console.log(this.db_hd[i]);
		}
	}
  this.tiencu = 0 ;
  this.tongcong = tonghoadon;
  this.test_tonghoadon = conlai;
  this.test_tratien = thanhtoan;
  this.test_conlai = 0;
 for (var i = this.db_dmkh.length - 1; i >= 0; i--) {
        if(this.db_dmkh[i].makh == makh){
          this.tenkhs = this.db_dmkh[i].tenkh;
        }

  }
	for (var i = this.db_hoadon.length - 1; i >= 0; i--) {
   		//console.log(this.db_hoadon[i].mahoadon + ' ');
       if(this.db_hoadon[i].id < id && this.db_hoadon[i].makh == makh){
         this.test_conlai = this.test_conlai + this.db_hoadon[i].conlai;
       }

	}
  
//console.log(this.test_tonghoadon + ' - ' + this.test_tratien + ' - ' + this.test_conlai);
	this.show = show;
	this.show_chua = chua;
	this.show_giao = giao;
}
list_back(chua : boolean, giao : boolean, show : boolean){
	this.show = show;
	this.show_chua = chua;
	this.show_giao = giao;
}
onPrint(){
  window.print();
}
onSort(col){
  this.sortBy = col;
  this.sortValue = -this.sortValue;
}
}
