import { Component, OnInit, OnDestroy } from '@angular/core';
import { DmkhService } from './../../../services/dmkh.service';
import { DmdiachiService } from './../../../services/dmdiachi.service';
import { Subscription } from 'rxjs';
import { dmkh } from './../../../models/dmkh.model';
import { dmdiachi } from './../../../models/dmdiachi.model';
import { Router } from '@angular/router';

@Component({
  selector: 'app-dmkh-add',
  templateUrl: './dmkh-add.component.html',
  styleUrls: ['./dmkh-add.component.css']
})
export class DmkhAddComponent implements OnInit, OnDestroy {

	public subscription : Subscription;
	
   public db : dmkh;
   public db_dmdiachi : dmdiachi[] = [];
 	public db_khachhang : dmkh[] = [];
   public dem : number = 0;
   public madiachi : string;
  constructor(
      public dmkhservice : DmkhService,
  		public dmdiachiservice : DmdiachiService,
  		public routerService : Router
  	) { }

  ngOnInit() {
  	this.db = new dmkh();
    this.subscription =  this.dmdiachiservice.getAlldmdiachi().subscribe((data: dmdiachi[]) =>{
      this.db_dmdiachi = data; 
    });
    this.subscription =  this.dmkhservice.getAlldmkh().subscribe((data: dmkh[]) =>{
      this.db_khachhang = data;
    });
  }

  ngOnDestroy(){
  	if(this.subscription)
  	{
  			this.subscription.unsubscribe();
  	}
  }
  onClickTendiachi(){
    this.dem =1;
    for (var i = this.db_khachhang.length - 1; i >= 0; i--) {
        if(this.db_khachhang[i].makh.slice(0,2)==this.madiachi)
        {
          this.dem ++;
        }
    }  
    if(this.dem <10)
    {
      this.db.makh = this.madiachi + '0' + this.dem;
    }else
    {
      this.db.makh = this.madiachi + this.dem;
    }
  }
  onAdd_dmkh(){
   	this.subscription = this.dmkhservice.add_dmkh(this.db).subscribe(data =>{
  		//console.log(data);
  		if(data)
  		{
  			this.routerService.navigate(['dmkh']);
  		}
  	});
  }

}
