import { Component, OnInit, OnDestroy } from '@angular/core';
import { DmhangService } from './../../../services/dmhang.service';
import { DmdiachiService } from './../../../services/dmdiachi.service';
import { Subscription } from 'rxjs';
import { dmhang } from './../../../models/dmhang.model';
import { Router } from '@angular/router';
@Component({
  selector: 'app-dmhang-add',
  templateUrl: './dmhang-add.component.html',
  styleUrls: ['./dmhang-add.component.css']
})
export class DmhangAddComponent implements OnInit, OnDestroy {

	public subscription : Subscription;
	//public db : dmhang[] = [];
 	public db : dmhang;
   public dmhangs : dmhang[] = [];
   public  tendiachi : string;
   public  madiachi : string;
   public dem : number =0;
  constructor(
    public dmhangservice : DmhangService,
  	public dmdiachiservice : DmdiachiService,
  	public routerService : Router

  	) { }

  ngOnInit() {
    this.subscription =  this.dmhangservice.getAlldmhang().subscribe((data: dmhang[]) =>{
      this.dmhangs = data;
    });
  	this.db = new dmhang();
  }
  ngOnDestroy(){
  	if(this.subscription)
  	{
  			this.subscription.unsubscribe();
  	}
  }
  onClickTenhang(){
    this.dem =1;
    console.log(this.madiachi);
    for (var i = this.dmhangs.length - 1; i >= 0; i--) {
        if(this.dmhangs[i].mahang.slice(0,2)==this.madiachi)
        {
          this.dem ++;
        }
    }  
    if(this.dem <10)
    {
      this.db.mahang = this.madiachi + '0' + this.dem;
    }else
    {
      this.db.mahang = this.madiachi + this.dem;
    }
  }
  onAdd_dmhang(){
   	this.subscription = this.dmhangservice.add_dmhang(this.db).subscribe(data =>{
  		//console.log(data);
  		if(data)
  		{
  			this.routerService.navigate(['dmhang']);
  		}
  	});
  }

}
