import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'chitiethd'
})
export class ChitiethdPipe implements PipeTransform {

  transform(hoadons: any[], mahd : string, id : boolean, makhs : string): any {
   if(!mahd)
   {
     if(makhs)
      {
       hoadons = hoadons.filter(item => 
         item.tinhtrang.toString().toLocaleLowerCase().indexOf(id.toString().toLocaleLowerCase()) !== -1);
       return hoadons.filter(item => 
         item.makh.toString().toLocaleLowerCase().indexOf(makhs.toString().toLocaleLowerCase()) !== -1); 

      }else
      {
         return hoadons.filter(item => 
         item.tinhtrang.toString().toLocaleLowerCase().indexOf(id.toString().toLocaleLowerCase()) !== -1);     
      }
   	 
   }else
   if(mahd)
   {
   		/*hoadons = hoadons.filter(x =>{
   			//console.log(x.id.toString().indexOf(id));
   			return x.id.toString().includes(id);
   		});*/
	    if(makhs)
      {
       hoadons = hoadons.filter(item => 
         item.tinhtrang.toString().toLocaleLowerCase().indexOf(id.toString().toLocaleLowerCase()) !== -1);
       hoadons = hoadons.filter(item => 
         item.mahoadon.toString().toLocaleLowerCase().indexOf(mahd.toString().toLocaleLowerCase()) !== -1); 
       return hoadons.filter(item => 
         item.makh.toString().toLocaleLowerCase().indexOf(makhs.toString().toLocaleLowerCase()) !== -1); 

      }else
      {
      hoadons = hoadons.filter(item => 
         item.tinhtrang.toString().toLocaleLowerCase().indexOf(id.toString().toLocaleLowerCase()) !== -1);
       return hoadons.filter(item => 
         item.mahoadon.toString().toLocaleLowerCase().indexOf(mahd.toString().toLocaleLowerCase()) !== -1);  
      }
   }

     }
}
