import { Injectable } from '@angular/core';
//import HttpClient vào để sử dụng
import { HttpClient } from '@angular/common/http';
//import Model để chứa dl
import { dmdiachi } from './../models/dmdiachi.model';
//import kiểu dữ liệu Observable
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class DmdiachiService {

  	public API : string = 'http://localhost:3000/dmdiachi';

  constructor(public http : HttpClient) { }
  //List danh sách  các mặt hàng
  getAlldmdiachi() {
  return this.http.get(this.API);
  } 
  getOnedmdiachi(id: number) {
    return this.http.get(`${this.API}/${id}`);
  }
  add_dmdiachi(dmdiachi : dmdiachi) {
	return this.http.post(this.API, dmdiachi);  	
  }
   delete_dmdiachi(id : number) {
      return this.http.delete(`${this.API}/${id}`);
  }
  uppdate_dmdiachi(dmdiachi : dmdiachi) {
      return this.http.put(`${this.API}/${dmdiachi.id}`,dmdiachi);
  }
}
